import React from 'react';
import Layout from '../../components/layout';
import SEO from '../../components/seo';
import MainHeader from '../../components/main-header';
import Footer from '../../components/footer';
import ProjectBlock from '../../components/project-block';
import Fade from 'react-reveal/Fade';
import NotFoundPage from '../404';

const data = { title: "О нас", des: "Узнайте больше об Abstract", url: "/ru/agency" };

const Project = ({ location }) => {
    if (location.state) {
        const title = location.state.title;
        const url = location.state.videoUrl;
        const description = location.state.description;
        return <Layout ru>
            <SEO title="Project" />
            <div className="dark-theme bg-project">
                <section className="container work-block no-margin">
                    <MainHeader title={title} />
                    <div className="column">
                        <Fade bottom>
                            <p className="description">{description}</p>
                        </Fade>
                    </div>
                </section>
            </div>
            <ProjectBlock title={title} videoUrl={url} />
            <Footer data={data} />
        </Layout>
    } else {
        return <NotFoundPage />
    }
};

export default Project;